@media (max-width: 1200px) {
	.menu {
		&_item {
			padding-right: 20px; }
		&_link {
			font-size: 13px; } }
	.promo {
		&_header {
			font-size: 50px; }
		&_subheader {
			font-size: 35px; } }
	.cause {
		&_block {
			align-items: flex-start; }
		&_descr {
			width: 300px; } } }
@media (max-width: 991px) {
	.menu {
		&_item {
			padding-right: 8px; }
		&_link {
			font-size: 10px; } }
	.subheader {
		&_logo {
			img {
				width: 100%; } }
		&_official {
			font-size: 11px; }
		&_phone {
			font-size: 16px; }
		&_btn {
			margin-top: 21px;
			width: 100%;
			height: 40px; } }
	.promo {
		&_header {
			font-size: 40px; }
		&_subheader {
			font-size: 30px; } }
	.cause {
		&_block {
			align-items: center;
			flex-direction: column; }
		&_descr {
			width: 300px;
			margin-top: 20px; } } }
@media (max-width: 767px) {
	.menu {
		flex-direction: column;
		justify-content: center;
		align-items: center;
		background-color: rgba(0,0 ,0 ,0.8 );
		position: fixed;
		top: 0;
		left: -100%;
		width: 50%;
		height: 100vh;
		padding: 20px 15px;
		transition: 0.6s all;
		&_active {
			left: 0; }
		&_item {
			padding-right: 0;
			border: none;
			margin: 10px; }
		&_link {
			font-size: 20px; } }
	.hamburger {
		display: block;
		position: absolute;
		right: 10px;
		top: 50%;
		transform: translateY(-50%);
		width: 26px;
		height: 12px;
		span {
			display: block;
			height: 2px;
			width: 100%;
			background-color: #ffffff;
			margin-bottom: 2px;
			transition: 0.5s all; }
		&_active {
			span {
				margin-bottom: -2px;
				&:nth-child(1) {
					transform: translateY(3px) rotate(-45deg); }
				&:nth-child(2) {
					display: none; }
				&:nth-child(3) {
					transform: translateY(3px) rotate(45deg); } } } }
	.xs-hidden {
		display: none; } }
